const reducer = (state = initialState, action) => {
  switch (action.type) {
    case "SET_FILETREE_CONTENT": {
      return {
        ...state,
        filetreeContent: action.payload.content,
        filetreeFileName: action.payload.fileName,
      };
    }
    case "SET_LOCATION_CONTENT": {
      return {
        ...state,
        locationContent: JSON.parse(action.payload.content),
        locationFileName: action.payload.fileName,
      };
    }
    case "CLEAN_FILES": {
      return {
        ...state,
        filetreeContent: undefined,
        locationContent: undefined,
      };
    }
    default: {
      return state;
    }
  }
};

export default reducer;
